import axios from 'axios';

// import { contextVar } from 'Components/Context/Context';

import React, { useRef, useState, useEffect, useContext } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import ReactToPrint from 'react-to-print-advanced';
import AdvertisementApiList from '../AdvertisementApiList';
import ComponentToPrint from './AdvtPaymentReceipt';
import useSetTitle from '../../../Components/GlobalData/UseSetTitle';
import ApiHeader from '../../../Components/ApiList/ApiHeader';
import AxiosInterceptors from '../../../Components/GlobalData/AxiosInterceptors';



function AdvtPaymentReceiptIndex() {

    ///////{***âœ… payment Id from use param...***}///////
    const { paymentId, wrkflowId } = useParams()


    console.log("param transcation id ..", paymentId)
    console.log("param wrkflow id ..", wrkflowId)

    const { getPaymentsuccessDetails } = AdvertisementApiList();

    const componentRef = useRef();
    const [paymentData, setpaymentData] = useState();
    // SETTING GLOBAL TITLE AT ONCE USING CUSTOM HOOK
    useSetTitle('Payment Receipt')

    useEffect(() => {
        fetchPaymentData();
    }, [])

    const fetchPaymentData = () => {
        const requestBody = {
            paymentId: paymentId,
            workflowId: wrkflowId,
        }

        console.log('before fetch payment receipt....', requestBody)
        AxiosInterceptors.post(`${getPaymentsuccessDetails}`, requestBody, ApiHeader())
            .then((response) => {
                // console.log("payment data", response.data.data);
                console.log("payment data 2", response);
                if (response.data.status) {

                    setpaymentData(response.data.data);
                } else {

                }
            })
            .catch((error) => {
                console.log(error);
            })
    }

    console.log("payment data", paymentData)

    return (
        <div>
            {/* <NonBlockingLoader show={show} /> */}
            {/* {
                show && <BarLoader />
            } */}
            <ReactToPrint
                trigger={() =>
                    <button className="float-right pl-4 pr-6 py-1 bg-sky-400 text-white font-medium text-xs leading-tight uppercase rounded   hover: focus: focus:outline-none focus:ring-0  active: transition duration-150 ease-in-out">Print</button>
                }
                content={() => componentRef.current}
            />
            {/* <ComponentToPrint module={module} ref={componentRef} paymentData={paymentData} /> */}
            <ComponentToPrint ref={componentRef} paymentData={paymentData} />
        </div>
    )
}

export default AdvtPaymentReceiptIndex